<template>
  <!-- 车辆管理 -->
  <div class="container">
    <div class="header">
      <el-button type="primary">批量导出</el-button>
      <el-input v-model="input" placeholder="请输入企业名称查询"></el-input>
    </div>
    <div class="con">
      <el-table
        :data="tableData"
        style="width: 100%"
        :row-class-name="tableRowClassName"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="enterprise" label="公司名称" width="210"></el-table-column>
        <el-table-column prop="contactPerson" label="联系人" width="110"></el-table-column>
        <el-table-column prop="phone" label="联系电话" width="180"></el-table-column>
        <el-table-column prop="createTime" label="授权时间" width="180"></el-table-column>
        <el-table-column prop="violation" label="违规次数" width="80"></el-table-column>
        <el-table-column prop="operat" fixed="right" label="操作" width="160">
          <template slot-scope="scope">
            <el-button size="mini" type="warning" @click="modifyCarShow = true">修改</el-button>
            <el-popconfirm title="确定删除该车辆信息？">
              <el-button size="mini" slot="reference" type="danger">删除</el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="buttonCon">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="1000">
        </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name: "passAnnal",
  data() {
    return {
      tableData: [
        {
          enterprise: "公司名称",
          contactPerson: "王小虎",
          phone: "18888888888",
          createTime: "2016-05-04 02:02:02",
          violation: "10"
        },
      ],
    };
  },
  methods: {
    goCarInfo() {
      this.$router.push(`carInfo`)
    },
    delList() {
      this.$message.error('请先选择需要删除的车辆');
    }
  },
};
</script>
<style scoped>
.header .el-button {
  margin: 0 10px;
}
.el-input {
  width: 200px;
}
.header {
    padding: 10px;
    background-color: #fff;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.buttonCon {
  margin-top: 10px;
  text-align: right;
}

.cell {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
</style>


